 Index page
*/

$colors: (
  "1": #e42421,
  "2": #bdd7ee,
  "3": #bdd7ee,
  "4": #bdd7ee,
  "5": #ffe698,
  "6": #ffe698,
  "7": #ffe698,
  "8": #f7cbac,
  "9": #f7cbac,
  "10": #bf910a,
  "11": #bf910a,
  "12": #d5dbe4,
  "13": #d5dbe4,
  "14": #2f5598,
  "15": #2f5598,
  "16": #203863,
  "17": #203863,
  "18": #afabab,
  "19": #afabab,
  "20": #f6ac7c,
  "21": #f6ac7c,
  "22": #673a8e,
  "23": #27ade4,
  "24": #29a951,
  "25": #385725,
  "26": #fab613,
  "27": #b6db9a,
  "28": #f2e500,
  "29": #cbc9e6,
  "30": #fbdfd4,
  "31": #fbdfd4,
  "32": #fbdfd4
);

.index-btn {
  position: absolute;
  right: 0.5rem;
  top: 0.5rem;
  width: 2.7rem;
  height: 2.7rem;
  background-color: #f7e561;
  border: 0.3rem solid #696969;
  border-radius: 50%;
  text-align: center;
  cursor: pointer;
  z-index: 1000;

  span {
    // width: 80%;
    height: 0.3rem;
    width: 1.4rem;
    left: 0.36rem;
    top: 0.4rem;
    display: inline-block;
    position: absolute;
    // top: 1rem;
    background-color: #333333;
    border-radius: 1rem;
    transition: 0.2s ease-in-out;
  }
  span:nth-child(2) {
    top: 0.85rem;
  }
  span:nth-child(3) {
    top: 1.3rem;
  }
  &.open {
    span:nth-child(2),
    span:first-child {
      transform: rotate(45deg);
    }
    span:first-child {
      top: 0.9rem;
    }
    span:nth-child(3) {
      transform: rotate(-45deg);
      top: 0.9rem;
    }
  }
}
.index-page {
  max-width: 70.707vh;
  overflow-y: scroll;
  height: 100vh;
  background-color: rgb(236, 236, 236);
  margin: auto;
  animation: fade 0.5s ease forwards;
  direction: rtl;
  .header {
    h1 {
      margin: 0;
      text-align: center;
      padding: 1rem 1rem 0 1rem;
      font-size: 2rem;
    }
    h5 {
      margin: 0;
      font-size: 1rem;
      text-align: center;
    }
  }
  .container {
    padding: 0.5rem;
    padding-bottom: 8rem;

    .row {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      .column {
        width: 33.3333333%;
        font-weight: bold;
        &.relax {
          width: 25%;
        }
      }
      &.expanded {
        box-shadow: #333 1px 1px 10px -4px;
        border-radius: 1rem;
        background-color: #fff4a2;
        @each $color_index, $color_value in $colors {
          &.color_#{$color_index} {
            background-color: $color_value;
          }
        }
      }
    }
    .letter-shape {
      max-width: 69px;
      min-height: 69px;
      margin: auto;
      border: 0.1rem solid #aeabab;
      box-shadow: #333 1px 1px 10px -4px;
      background-color: rgb(255, 255, 255);
      border-radius: 0.5rem;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 1.4rem;
      font-weight: bold;
      cursor: pointer;
      &:active, &:hover {
        background-color: #cbcbcb;
      }
    }
  
    .letter-box-container {
      &.active .letter-box{
        border-width: 0.4rem;

      }
    }
    .letter-box-container,
    .letter-shape-container {
      width: 100%;
      height: 100%;
      padding: 0.3rem;
      transition: 0.5s ease-in-out;
      // overflow: hidden;
      @each $color_index, $color_value in $colors {
        .letter-box.color_#{$color_index} {
          border-color: $color_value;
        }
      }
      .letter-box {
        min-height: 69px;
        background-color: rgb(243, 243, 243);
        border: 0.1rem solid #aeabab;
        box-shadow: #333 1px 1px 10px -4px;
        border-radius: 0.5rem;
        display: flex;
        text-align: center;
        justify-content: center;
        align-items: center;
        font-size: 1.1srem;
        transition: 0.5s ease-in-out;
        font-weight: bold;
        padding: 0.3rem;
        height: 100%;
        cursor: pointer;
        &.active {
          background-color: rgb(213, 213, 213);
        }
      }
      
    }
  }
}

@keyframes show-expand {
  0% {
    background-color: transparent;
  }
  100% {
    background-color: #fff4a2;
   
  }
}
