.main-app {
  height: 100%;
  min-height: -webkit-fill-available;
  background-color: #f7f7f7;
  margin: auto;
  max-width: 70.707vh;
  box-shadow: 0 0 19px -4px #000;

  background: url('./imgs/wallpaper.jpg');
  background-repeat: repeat;
  // background-attachment: fixed;

  @import "./Header.scss";

  @import "./SketchBook.scss";

  @import "./IndexPage.scss";

  @import "./InfoPage.scss";

  .controls {
    height: 5%;

    .wrapper {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-self: center;
      margin-top: 1rem;

      .button {
        background-color: #dcdcdc;
        display: flex;
        justify-content: center;
        align-self: center;
        border-radius: 50%;
        cursor: pointer;
        box-shadow: -4px 1px 3px 0px #555;

        &:active {
          background-color: #c5c5c5;
          border-color: #000000;
        }

        img {
          width: 3.8rem;
          height: auto;
          align-self: flex-start;
        }
      }
    }
  }

  .footer {
    position: relative;
    height: 17%;
    display: flex;
    justify-content: center;
    align-items: center;

    .container {
      position: relative;
      width: 100%;
      height: 100%;
      max-width: 55vh;

      .shape {
        position: absolute;

        &:after {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
        }

        &.plant {
          top: 0vh;
          left: 2vh;
          width: 13vh;
        }

        &.pencil {
          top: -5.5vh;
          right: 8vh;
          width: 1vh;
          transform: rotate(-120deg);
        }

        &.pen {
          top: -5.8vh;
          right: 12vh;
          width: 1.5vh;
          transform: rotate(62deg);
        }

        &.pen2 {
          top: 2vh;
          right: 12vh;
          width: 1.4vh;
          transform: rotate(-77deg);
        }
      }
    }
  }

  .passed-modal {
    z-index: 100;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    .blur-box {
      background-color: #32323299;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    .passed-modal-wrapper {
      position: fixed;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      box-shadow: 0 0 19px -4px #000;
      max-width: 100vw;
      max-height: 100vw;
      height: 50vh;
      width: 50vh;
      background: url(./imgs/wallpaper.jpg);
      background-repeat: repeat;
      background-position: center;
      // background-attachment: fixed;
      -o-background-size: cover;
      -moz-background-size: cover;
      -webkit-background-size: cover;
      background-size: 100% 100%;
      border-radius: 1rem;
      border: 2px solid #313131;
      z-index: 100000;
      display: flex;
      flex-wrap: wrap;
      .header {
        height: 25%;
        width: 100%;
        text-align: center;
        font-size: 2.2vh;
      }

      .body {
        width: 100%;
        height: 50%;
        display: flex;
        justify-content: center;
        align-content: center;
        flex-wrap: wrap;

        .button {
          background-color: #dcdcdc;
          display: flex;
          justify-content: center;
          align-self: center;
          border-radius: 50%;
          cursor: pointer;
          user-select: none;
          box-shadow: -4px 1px 3px 0px #555;
          &:active {
            background-color: #cccccc;
          }
          img {
            width: 6rem;
            height: auto;
            align-self: flex-start;
          }
        }
      }
      .footer {
        display: flex;
        width: 100%;
        height: 25%;
        // margin-top: 2rem;
        padding-bottom: 1rem;
        flex-wrap: wrap;
        font-size: 2vh;
        h4 {
          text-align: center;
          width: 100%;
          margin: 0;
        }
        .button.sm {
          display: flex;
          cursor: pointer;
          justify-content: center;
          align-self: center;
          box-shadow: -4px 1px 3px 0px #555;
          border-radius: 50%;
          user-select: none;
          img {
            width: 2.1rem;
            height: auto;
            align-self: flex-start;
          }
        }
      }
    }
  }

  .confetti-container {
    @keyframes confettiRain {
      0% {
        opacity: 1;
        margin-top: -100vh;
        margin-left: -200px;
      }

      100% {
        opacity: 1;
        margin-top: 100vh;
        margin-left: 200px;
      }
    }

    .confetti {
      opacity: 0;
      position: absolute;
      width: 1rem;
      height: 1.5rem;
      transition: 500ms ease;
      animation: confettiRain 5s infinite;
    }
  }

  .button {
    transition: ease-in-out 0.1s all;
    &:hover {
      // border: 3px solid #888;
      transform: scale(0.95);
    }
    &:active {
      transform: scale(0.9);
    }
  }

  canvas.disabled {
    opacity: 0.7;
  }

  .letter-color-1 {
    color: #e30611;
  }
  .letter-color-2 {
    color: #049a3b;
  }
  .letter-color-3 {
    color: #3256a2;
  }
}

.hidden {
  visibility: hidden;
}
.animate-photo {
  animation: idne ease 800ms forwards;
}

@keyframes idne {
  0% {
    transform: scale(1) rotate(0deg);
  }

  50% {
    transform: scale(1.1) rotate(15deg);
  }

  100% {
    transform: scale(1) rotate(0deg);
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
