& > .header {
  position: relative;
  height: 28%;
  display: flex;
  justify-content: center;
  align-items: center;

  .container {
    position: relative;
    width: 100%;
    height: 100%;
    max-width: 55vh;

    .shapes {
      height: 77.6%;
    }

    .shape {
      position: absolute;

      &:after {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
      }

      &.logo {
        top: 2vh;
        left: 1vh;
        width: 14vh;
        cursor: pointer;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        img {
          width: 100%;
          height: auto;
          align-self: flex-start;
        }
        h3 {
          line-height: 1.6rem;
          margin: 0;
          text-align: center;
        }
      }

      &.bear {
        top: 13vh;
        right: 1vh;
        width: 9vh;
        cursor: pointer;
        img {
          height: auto;
          align-self: flex-start;
          transform: rotate(18deg);
        }
      }

      &.notes {
        top: 2vh;
        right: 9.5vh;
        width: 10vh;
        cursor: pointer;

        img {
          height: auto;
          align-self: flex-start;
          transform: rotate(20deg);
        }
      }

      // &.clips {
      //   top: 13vh;
      //   right: 18vh;
      //   width: 5vh;
      //   cursor: pointer;

      //   img {
      //     transform: rotate(-110deg);
      //   }
      // }
    }
  }

  .title-container {
    width: 100%;
    height: 22.4%;

    .title {
      width: 27.1vh;
      height: 5.3vh;
      background-color: #dcdcdc;
      border: #44444489 1px solid;
      border-radius: 8px;
      margin: auto;
      height: 6vh;
      text-align: center;
      font-size: 1.2rem;
      font-weight: bold;
    }
  }
}
