.info-page-container {
  z-index: 1000;
  max-width: 70.707vh;
  overflow-y: scroll;
  height: 100vh;
  background: url(./imgs/wallpaper.jpg);
  margin: auto;
  animation: fade 0.5s ease forwards;
  direction: rtl;
  position: relative;
  .controls {
    text-align: center;
    width: 100%;
    padding: 1rem 0;
    color: green;
  }
  .button {
    background-color: #dcdcdc;
    display: inline-flex;
    justify-content: center;
    align-self: center;
    border-radius: 50%;
    cursor: pointer;
    box-shadow: -4px 1px 3px 0px #555;

    &:active {
      background-color: #c5c5c5;
      border-color: #000000;
    }

    img {
      height: auto;
      align-self: flex-start;
      width: 8.4rem;
    }
  }
  .info-page-wrapper {
    display: flex;
    flex-wrap: wrap;
    padding: 2rem;
    padding-top: 3.5rem;
    h2,
    h4,
    p {
      margin: 0;
    }
    .header {
      width: 100%;
    }
    .content {
      width: 100%;
      ul {
        list-style: none;
        padding: 0;
        li {
          padding: 0.5rem;
          margin-bottom: 1rem;
          color: #049a3b;
          p {
            color: #222;
            font-weight: bold;
            margin-bottom: 1rem;
          }
          h4 {
            font-size: 1.2rem;
          }
          &:first-child {
            color: #e30611;
          }
          &:last-child {
            color: #3256a2;
          }
          .item{
            img {
              height: auto;
              align-self: flex-start;
              width: 100%;
            }
          }
        }
      }
    }
  }
}
