.sketch-book {
  height: 50%;
  position: relative;
  padding-top: 1rem;

  .container {
    height: 100%;
    padding: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;

    .wrapper {
      height: 100%;
      border: 2vh solid #42b0e4;
      border-radius: 2vh;
      max-width: 44vh;
      max-height: 100vw;
      width: 100%;
      position: relative;
      box-shadow: -4px 4px 10px -2px rgb(50, 50, 50);
      transform-style: preserve-3d;
      perspective: 600px;
      &.animate-book {
        animation: shake 500ms linear forwards;
      }

      @keyframes shake {
        0% {
          transform: rotate(0deg);
        }
        15% {
          transform: rotate(3deg);
        }
        48% {
          transform: rotate(-3deg);
        }
        80% {
          transform: rotate(3deg);
        }
        100% {
          transform: rotate(0deg);
        }
      }

      // &:after {
      //   content: "";
      //   position: absolute;
      //   left: 0;
      //   top: 0;
      //   width: 100%;
      //   height: 100%;
      // }

      &:before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        box-shadow: 0 0 7px -1px rgb(50 50 50);
        background-color: white;
      }

      .book-header {
        $height: 3rem;
        width: 100%;
        height: $height + 0.5rem;
        position: absolute;
        left: 0;
        top: -2rem;
        overflow: hidden;
        transform-style: preserve-3d;
        transform: translateZ(1px);

        .book-header-wrapper {
          width: 100%;
          display: flex;
          flex-wrap: wrap;
          justify-content: space-evenly;
          // gap: 1rem;
          padding: 0 1rem;
          & > * + * {
            margin-left: 1rem;
            margin-bottom: 1rem;
          }
          span {
            display: block;
            width: 0.4rem;
            height: $height;
            position: relative;

            .line {
              height: 100%;
              width: 100%;
              border-radius: 2rem;
              background-color: #f58f22;
              z-index: 10;
              box-shadow: 0 0 7px 0px rgb(50, 50, 50);
            }

            .dot {
              display: block;
              bottom: -0.25rem;
              left: 50%;
              transform: translateX(-50%);
              width: 1rem;
              height: 1rem;
              background-color: #61789e;
              border-radius: 50%;
              position: absolute;
              z-index: 1;
            }
          }
        }
      }

      .sketch-book-canvas-container {
        max-width: 44vh;
        position: relative;
        width: 100%;
        height: 100%;
        background-color: white;
        box-shadow: 0 0 4px -1px rgb(92, 92, 92);
        transform-style: preserve-3d;
        transform-origin: top center;

        &:before {
          content: "";
          background-color: white;
          display: block;
          position: absolute;
          width: 100%;
          height: 100%;
          transform: translateZ(-1px) rotate(180deg);
        }

        .react-p5-wrapper {
          width: 100%;
          height: 100%;
          position: relative;

          &:before {
            content: "";
            position: absolute;
            width: 100%;
            border-top: #22222288 dashed 0.2rem;
            top: 66%;
          }

          canvas {
            transform-style: preserve-3d;
            transform: translateZ(1px);
          }
        }

        &.animate {
          animation: ease flip 800ms forwards;
        }

        @keyframes flip {
          0% {
            transform: rotateX(0deg) translateY(0);
          }

          100% {
            opacity: 0.5;
            transform: rotateX(255deg) translateY(25px);
          }
        }
      }
    }
  }
}
